import Lines from '@/components/common/Lines';
export default {
	components: {
		Lines
	},
	data() {
		return {
			// 输入搜索词
			keyWord: '',
			//搜索过的词
			searchData: []
		};
	},
	methods: {
		search(){
			if(this.keyWord !== ''){
				uni.navigateTo({
					url:`../Result/Result?keyword=${this.keyWord}`,
				})
				uni.hideKeyboard()
				this.addSearch()
			}else{
				uni.showToast({
					title:'输入框不能为空',
					icon:'none',
					duration: 1500
				})
			}
			
		},
		addSearch(){
			// 查询 元素 在列表 里的索引 没有 -1
			let idx = this.searchData.indexOf( this.keyWord)
			if(idx < 0){
				this.searchData.unshift( this.keyWord)
			}else{
				this.searchData.unshift(this.searchData.splice(idx,1)[0])
			}
			uni.setStorage({
				key:"searchData",
				data:JSON.stringify(this.searchData)
			})
			
		},
		clearData(){
				uni.showModal({
					title: '重要提示',
					content:'是否清除记录？',
					confirmColor:'#F76260',
					cancelColor:'#333333',
					success: (res) => {
						if(res.confirm){
							uni.removeStorage({
								key:"searchData"
							})
							this.searchData = []
						}
					}
				})
		},
		//tag 点击进入 搜索页面
		tagClick(item){
			uni.navigateTo({
				url: `../Result/Result?keyword=${item} `
			})
		}
	},
	//生命周期
	onLoad() {
		uni.getStorage({
			key:"searchData",
			success: (res) => {
				this.searchData = JSON.parse(res.data)
			}
		})
	},
	onNavigationBarButtonTap(e) {
		this.search()
	},
	onNavigationBarSearchInputChanged(e){
		this.keyWord = e.text
	},
	onNavigationBarSearchInputConfirmed(e){
		this.search()
	}
};
